.fee-container {
  position: relative;
  padding: 0px 0; /* –£–≤–µ–ª–∏—á–∏–≤–∞–µ–º –æ—Ç—Å—Ç—É–ø—ã —Å–≤–µ—Ä—Ö—É –∏ —Å–Ω–∏–∑—É */
  background-color: mediumslateblue;
  flex-direction: column; /* –†–∞—Å–ø–æ–ª–∞–≥–∞–µ–º –∫–∞—Ä—Ç–æ—á–∫–∏ –ø–æ –≤–µ—Ä—Ç–∏–∫–∞–ª–∏ */
  align-items: center; /* –¶–µ–Ω—Ç—Ä–∏—Ä—É–µ–º –∫–∞—Ä—Ç–æ—á–∫–∏ –ø–æ –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª–∏ */
}

.header-text-huy {
  text-align: center;
  color: rgb(210, 99, 89);
  
}

.header-text-huy h5 {
  font-family: 'TRIAL_Oceanic', sans-serif;
  font-weight: 500;
  font-size: 70px;
  padding-top: 10px;
}

.cards-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  flex-wrap: wrap;
  margin-top: 20px; /* –¥–ª—è –æ—Ç—Å—Ç—É–ø–∞ –æ—Ç —Ç–µ–∫—Å—Ç–∞ */
  margin-left: 20px;
  background-color: rgb(0, 255, 60);
  padding-bottom: 30px;
}

.fee-card {
  margin-right: 0px;
  width: 428px; /* –®–∏—Ä–∏–Ω–∞ –∫–∞—Ä—Ç–æ—á–∫–∏ */
  height: 600px; /* –í—ã—Å–æ—Ç–∞ –∫–∞—Ä—Ç–æ—á–∫–∏ */
  background: #f0f0f0;
  border-radius: 30px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  position: relative;
  transition: all 0.3s ease;
}

.card-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.card-text-wrapper {
  position: absolute;
  bottom: 0px;
  left: 0;
  right: 0;
  text-align: center;
  color: #fff;
}

.card-text {
  background-color: rgba(0, 0, 0, 0.5);
  padding: 10px;
  border-radius: 25px;
  font-family: "Raleway", sans-serif;
  font-weight: 300;
  font-style: normal;
  font-size: 28px;
}

/* –ú–æ–±–∏–ª—å–Ω–∞—è –∞–¥–∞–ø—Ç–∞—Ü–∏—è */
