/* ABOUT */
/* –°—Ç–∏–ª–∏ –¥–ª—è —Ä–∞–∑–¥–µ–ª–∞ "–û–±–æ –º–Ω–µ" */
.directions {
  background-color: var(--white);
  padding: 0px 0; /* –£–≤–µ–ª–∏—á–∏–≤–∞–µ–º –æ—Ç—Å—Ç—É–ø—ã —Å–≤–µ—Ä—Ö—É –∏ —Å–Ω–∏–∑—É */
  font-family: "Raleway", sans-serif;
  font-weight: 400;
  font-style: normal;
  background-color: gold;
}

.directions-content {
  width: 60%;
  margin: 0 auto;
  padding: 10px;
  display: flex;
  flex-direction: column; /* –†–∞—Å–ø–æ–ª–∞–≥–∞–µ–º –∫–∞—Ä—Ç–æ—á–∫–∏ –ø–æ –≤–µ—Ä—Ç–∏–∫–∞–ª–∏ */
  align-items: center; /* –¶–µ–Ω—Ç—Ä–∏—Ä—É–µ–º –∫–∞—Ä—Ç–æ—á–∫–∏ –ø–æ –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª–∏ */
}

.directions-text {
  flex: 1; /* –ó–∞–Ω–∏–º–∞–µ—Ç –ø–æ–ª–æ–≤–∏–Ω—É —à–∏—Ä–∏–Ω—ã */
  color: black; /* –ß–µ—Ä–Ω—ã–π —Ç–µ–∫—Å—Ç */
  margin-top: 0px;
  margin-bottom: 10px;
}

.directions-text p {
  font-size: 24px; /* –£–≤–µ–ª–∏—á–∏–≤–∞–µ–º —Ä–∞–∑–º–µ—Ä —à—Ä–∏—Ñ—Ç–∞ */
}

/* –ó–∞–≥–æ–ª–æ–≤–æ–∫ */
.directions-title-1 {
  flex: 1;
  color: rgb(210, 99, 89);
  display: flex;
  justify-content: flex-end; /* –í—ã—Ä–∞–≤–Ω–∏–≤–∞–Ω–∏–µ –ø–æ –ø—Ä–∞–≤–æ–º—É –∫—Ä–∞—é */
  align-items: center;
  padding-right: 0px;
}

.directions-title-1 h2 {
  font-family: 'TRIAL_Oceanic', sans-serif;
  font-weight: 700;
  font-size: 70px; /* –†–∞–∑–º–µ—Ä —à—Ä–∏—Ñ—Ç–∞ –∑–∞–≥–æ–ª–æ–≤–∫–∞ */
}

@media (max-width: 1360px) {
  .directions-content {
    width: 90%;
    padding: 10px 0px;
    display: flex;
    overflow-x: hidden; /* –°–∫—Ä—ã–≤–∞–µ—Ç –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω—ã–π —Å–∫—Ä–æ–ª–ª */
  }
  .directions-text p {
    font-size: 24px; /* –£–≤–µ–ª–∏—á–∏–≤–∞–µ–º —Ä–∞–∑–º–µ—Ä —à—Ä–∏—Ñ—Ç–∞ */
  }
  .directions-title-1 h2 {
    font-size: 70px; /* –†–∞–∑–º–µ—Ä —à—Ä–∏—Ñ—Ç–∞ –∑–∞–≥–æ–ª–æ–≤–∫–∞ */
    margin-top: 20px;
  }
  .directions-text {
    text-align: start;
    margin-bottom: 10px;
  }
}
@media (max-width: 1050px) {
  .directions-text p {
    font-size: 22px; /* –£–≤–µ–ª–∏—á–∏–≤–∞–µ–º —Ä–∞–∑–º–µ—Ä —à—Ä–∏—Ñ—Ç–∞ */
  }
  .directions-title-1 h2 {
