/* –°—Ç–∏–ª–∏ –¥–ª—è —Ä–∞–∑–¥–µ–ª–∞ "–û–±—Ä–∞–∑–æ–≤–∞–Ω–∏–µ" */
.working {
  background-color: var(--white);
  padding: 0px 0; /* –û—Ç—Å—Ç—É–ø—ã —Å–≤–µ—Ä—Ö—É –∏ —Å–Ω–∏–∑—É */
  font-family: "Raleway", sans-serif;
  font-weight: 400;
  font-style: normal;
  background-color: brown;
}

.working-content {
  max-width: 60%; /* –§–∏–∫—Å–∏—Ä–æ–≤–∞–Ω–Ω–∞—è —à–∏—Ä–∏–Ω–∞ –¥–ª—è –∫–æ–Ω—Ç–µ–Ω—Ç–∞ */
  margin: 0 auto; /* –¶–µ–Ω—Ç—Ä–∏—Ä–æ–≤–∞–Ω–∏–µ –∫–æ–Ω—Ç–µ–Ω—Ç–∞ */
  padding: 20px; /* –û—Ç—Å—Ç—É–ø—ã –≤–Ω—É—Ç—Ä–∏ –±–ª–æ–∫–∞ */
  display: flex; /* –ò—Å–ø–æ–ª—å–∑—É–µ–º Flexbox –¥–ª—è —Ä–∞–∑–¥–µ–ª–µ–Ω–∏—è –Ω–∞ –¥–≤–µ —á–∞—Å—Ç–∏ */
  background-color: chartreuse;
}

.working-title-1 {
  font-size: 50px;
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 10px;
}

.working-title-1 h2 {
  font-size: 50px;
  font-family: "Dela Gothic One", sans-serif;
  font-weight: 400;
  font-style: normal;
}

.working-text {
  flex: 1; /* –ó–∞–Ω–∏–º–∞–µ—Ç –ø–æ–ª–æ–≤–∏–Ω—É —à–∏—Ä–∏–Ω—ã */
  color: var(--black); /* –ß–µ—Ä–Ω—ã–π —Ç–µ–∫—Å—Ç */
  text-align: left; /* –í—ã—Ä–∞–≤–Ω–∏–≤–∞–Ω–∏–µ —Ç–µ–∫—Å—Ç–∞ –ø–æ –ª–µ–≤–æ–º—É –∫—Ä–∞—é */
  padding-left: 20px;
}

.working-text ul {
  list-style-position: outside; /* –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º –ø–æ–∑–∏—Ü–∏—é –º–∞—Ä–∫–µ—Ä–æ–≤ –≤–Ω–µ –±–ª–æ–∫–∞ */
  padding-left: 20px; /* –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º –æ—Ç—Å—Ç—É–ø –¥–ª—è —Å–ø–∏—Å–∫–∞ */
  margin-left: 0px; /* –£–±–∏—Ä–∞–µ–º –æ—Ç—Å—Ç—É–ø—ã —É —Å–ø–∏—Å–∫–∞ */
}

.working-text li {
  margin-left: 0; /* –£–±–∏—Ä–∞–µ–º –æ—Ç—Å—Ç—É–ø —Å–ª–µ–≤–∞ –¥–ª—è —ç–ª–µ–º–µ–Ω—Ç–æ–≤ —Å–ø–∏—Å–∫–∞ */
  padding-left: 0; /* –£–±–∏—Ä–∞–µ–º –æ—Ç—Å—Ç—É–ø –¥–ª—è —Ç–µ–∫—Å—Ç–∞ —Ä—è–¥–æ–º —Å –º–∞—Ä–∫–µ—Ä–æ–º */
  font-size: 24px;
  line-height: 1.5; /* –£–≤–µ–ª–∏—á–∏–≤–∞–µ–º –º–µ–∂—Å—Ç—Ä–æ—á–Ω—ã–π –∏–Ω—Ç–µ—Ä–≤–∞–ª –¥–ª—è –ª—É—á—à–µ–≥–æ –≤—ã—Ä–∞–≤–Ω–∏–≤–∞–Ω–∏—è */
  list-style-type: "üó∏ ";
}

.working-text p {
  margin-top: 30px;
  font-family: "Raleway", sans-serif;
  font-weight: 600;
  font-style: normal;
  font-size: 26px;
}

.working-title-1 {
  font-size: 50px; /* –†–∞–∑–º–µ—Ä —Ç–µ–∫—Å—Ç–∞ –∫–Ω–æ–ø–∫–∏ */
  flex: 1; /* –ó–∞–Ω–∏–º–∞–µ—Ç –≤—Ç–æ—Ä—É—é –ø–æ–ª–æ–≤–∏–Ω—É —à–∏—Ä–∏–Ω—ã */
  color: var(--coral);
  right: 5%; /* –†–∞—Å–ø–æ–ª–æ–∂–µ–Ω–∏–µ —Ç–µ–∫—Å—Ç–∞ –Ω–µ–º–Ω–æ–≥–æ –æ—Ç—Å—Ç—É–ø–∞–µ—Ç –æ—Ç –ø—Ä–∞–≤–æ–≥–æ –∫—Ä–∞—è */
  transform: translateY(40%); /* –¶–µ–Ω—Ç—Ä–∏—Ä–æ–≤–∞–Ω–∏–µ —Ç–µ–∫—Å—Ç–∞ –ø–æ –≤–µ—Ä—Ç–∏–∫–∞–ª–∏ */
}

.working-title-1 h2 {
  font-family: 'TRIAL_Oceanic', sans-serif;
  font-weight: 500;
  font-size: 70px; 
}



/* –ê–¥–∞–ø—Ç–∞—Ü–∏—è –ø–æ–¥ –º–æ–±–∏–ª—å–Ω—ã–µ —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞ */
@media (max-width: 1360px) {
  .working-title-1 h2 {
    font-size: 70px; 
  }
  .working-text p {
    margin-top: 30px;
    font-size: 26px;
    margin-bottom: 10px;
  }
  .working-content {
    flex-direction: column;
    max-width: 90%; /* –§–∏–∫—Å–∏—Ä–æ–≤–∞–Ω–Ω–∞—è —à–∏—Ä–∏–Ω–∞ –¥–ª—è –∫–æ–Ω—Ç–µ–Ω—Ç–∞ */
    padding: 10px 0px;
    overflow-x: hidden; /* –°–∫—Ä—ã–≤–∞–µ—Ç –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω—ã–π —Å–∫—Ä–æ–ª–ª */
  }
  .working-text li {
    font-size: 24px;
  }
  .working-text ul {
    
    padding-left: 0px; /* –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º –æ—Ç—Å—Ç—É–ø –¥–ª—è —Å–ø–∏—Å–∫–∞ */
    
  }
}

@media (max-width: 1050px) {
