.header {
  width: 100%; /* –ü–æ–ª–Ω–∞—è —à–∏—Ä–∏–Ω–∞ —ç–∫—Ä–∞–Ω–∞ */
  height: 50vw; /* –§–∏–∫—Å–∏—Ä–æ–≤–∞–Ω–Ω–∞—è –≤—ã—Å–æ—Ç–∞ */
  overflow: hidden; /* –ß—Ç–æ–±—ã –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ –Ω–µ –≤—ã—Ö–æ–¥–∏–ª–æ –∑–∞ –≥—Ä–∞–Ω–∏—Ü—ã */
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Raleway", sans-serif;
  font-weight: 400;
  font-style: normal;
  background-color: aqua;
}

/* –°—Ç–∏–ª–∏ –¥–ª—è —Ç–µ–∫—Å—Ç–∞ –ø–æ–≤–µ—Ä—Ö –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è */
.header-text {
  position: absolute;
  top: 50%;
  right: 5%; /* –†–∞—Å–ø–æ–ª–æ–∂–µ–Ω–∏–µ —Ç–µ–∫—Å—Ç–∞ –Ω–µ–º–Ω–æ–≥–æ –æ—Ç—Å—Ç—É–ø–∞–µ—Ç –æ—Ç –ø—Ä–∞–≤–æ–≥–æ –∫—Ä–∞—è */
  transform: translateY(-50%); /* –¶–µ–Ω—Ç—Ä–∏—Ä–æ–≤–∞–Ω–∏–µ —Ç–µ–∫—Å—Ç–∞ –ø–æ –≤–µ—Ä—Ç–∏–∫–∞–ª–∏ */
  color: white; /* –¶–≤–µ—Ç —Ç–µ–∫—Å—Ç–∞ */
  text-align: right;
  width: 45%;
}

.header-text h1 {
  font-size: 70px; /* –†–∞–∑–º–µ—Ä –∑–∞–≥–æ–ª–æ–≤–∫–∞ */
  margin: 0;
  font-family: 'TRIAL_Oceanic', sans-serif;
  font-weight: 700;
}

.header-text p {
  font-size: 24px; /* –†–∞–∑–º–µ—Ä —Ç–µ–∫—Å—Ç–∞ –ø–æ–¥ –∑–∞–≥–æ–ª–æ–≤–∫–æ–º */
  margin-left: 100px;
  margin-top: 40px;
  font-family: "Raleway", sans-serif;
  font-weight: 300;
  font-style: normal;
}

/* –°—Ç–∏–ª–∏ –¥–ª—è –∫–Ω–æ–ø–∫–∏ */
.header-button {
  display: inline-block;
  margin-top: 50px; /* –û—Ç—Å—Ç—É–ø —Å–≤–µ—Ä—Ö—É */
  padding: 15px 30px; /* –û—Ç—Å—Ç—É–ø—ã –≤–Ω—É—Ç—Ä–∏ –∫–Ω–æ–ø–∫–∏ */
  background-color: rgba(255, 255, 255, 0.6); /* –ü–æ–ª—É–ø—Ä–æ–∑—Ä–∞—á–Ω—ã–π —Ñ–æ–Ω –∫–Ω–æ–ø–∫–∏ */
  color: var(--blue); /* –¶–≤–µ—Ç —Ç–µ–∫—Å—Ç–∞ */
  border: none; /* –ë–µ–∑ —Ä–∞–º–∫–∏ */
  border-radius: 50px; /* –û–∫—Ä—É–≥–ª–µ–Ω–Ω—ã–µ —É–≥–ª—ã */
  font-family: "Raleway", sans-serif;
  font-weight: 600;
  font-style: normal;
  font-size: 22px;
  text-decoration: none; /* –£–±–∏—Ä–∞–µ–º –ø–æ–¥—á–µ—Ä–∫–∏–≤–∞–Ω–∏–µ */
  position: relative;
  overflow: hidden; /* –ß—Ç–æ–±—ã –∞–Ω–∏–º–∞—Ü–∏—è –Ω–µ –≤—ã—Ö–æ–¥–∏–ª–∞ –∑–∞ –≥—Ä–∞–Ω–∏—Ü—ã –∫–Ω–æ–ø–∫–∏ */
  transition: background-color 1s ease, color 1s ease; /* –ü–ª–∞–≤–Ω–∞—è –∞–Ω–∏–º–∞—Ü–∏—è */
}

.header-button:hover {
  background-color: var(--blue); /* –ò–∑–º–µ–Ω–µ–Ω–∏–µ —Ü–≤–µ—Ç–∞ —Ñ–æ–Ω–∞ –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏ */
  color: #fff; /* –ò–∑–º–µ–Ω–µ–Ω–∏–µ —Ü–≤–µ—Ç–∞ —Ç–µ–∫—Å—Ç–∞ –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏ */
}

.header-image {
  width: 100%;
  height: 100%;
  object-fit: cover; /* –ü–æ–¥–≥–æ–Ω—è–µ—Ç –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ –ø–æ –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä—É, —Å–æ—Ö—Ä–∞–Ω—è—è –ø—Ä–æ–ø–æ—Ä—Ü–∏–∏ */
  position: absolute;
  top: 0;
