/* –°—Ç–∏–ª–∏ –¥–ª—è —Ä–∞–∑–¥–µ–ª–∞ "–û–±—Ä–∞–∑–æ–≤–∞–Ω–∏–µ" */
.education {
  background-color: var(--coral);
  padding: 0px 0; /* –û—Ç—Å—Ç—É–ø—ã —Å–≤–µ—Ä—Ö—É –∏ —Å–Ω–∏–∑—É */
  font-family: "Raleway", sans-serif;
  font-weight: 400;
  font-style: normal;
  background-color: crimson;
}

.education-content {
  width: 60%; /* –§–∏–∫—Å–∏—Ä–æ–≤–∞–Ω–Ω–∞—è —à–∏—Ä–∏–Ω–∞ –¥–ª—è –∫–æ–Ω—Ç–µ–Ω—Ç–∞ */
  margin: 0 auto; /* –¶–µ–Ω—Ç—Ä–∏—Ä–æ–≤–∞–Ω–∏–µ –∫–æ–Ω—Ç–µ–Ω—Ç–∞ */
  padding: 20px; /* –û—Ç—Å—Ç—É–ø—ã –≤–Ω—É—Ç—Ä–∏ –±–ª–æ–∫–∞ */
  display: flex; /* –ò—Å–ø–æ–ª—å–∑—É–µ–º Flexbox –¥–ª—è —Ä–∞–∑–¥–µ–ª–µ–Ω–∏—è –Ω–∞ –¥–≤–µ —á–∞—Å—Ç–∏ */
  flex-wrap: wrap; /* –û–±–µ—Å–ø–µ—á–∏–≤–∞–µ–º –æ–±—Ç–µ–∫–∞–Ω–∏–µ —ç–ª–µ–º–µ–Ω—Ç–æ–≤ */
}
.education-text {
  flex: 1; /* –ó–∞–Ω–∏–º–∞–µ—Ç –ø–æ–ª–æ–≤–∏–Ω—É —à–∏—Ä–∏–Ω—ã */
  color: var(--white); /* –ß–µ—Ä–Ω—ã–π —Ç–µ–∫—Å—Ç */
  text-align: left; /* –í—ã—Ä–∞–≤–Ω–∏–≤–∞–Ω–∏–µ —Ç–µ–∫—Å—Ç–∞ –ø–æ –ª–µ–≤–æ–º—É –∫—Ä–∞—é */
}

.education-text p {
  font-size: 28px;
  margin-top: 20px;
  margin-bottom: 10px;
  font-weight: 600;
}

.education-text ul {
  list-style-position: outside; /* –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º –ø–æ–∑–∏—Ü–∏—é –º–∞—Ä–∫–µ—Ä–æ–≤ –≤–Ω–µ –±–ª–æ–∫–∞ */
  padding-left: 20px; /* –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º –æ—Ç—Å—Ç—É–ø –¥–ª—è —Å–ø–∏—Å–∫–∞ */
  margin: 0; /* –£–±–∏—Ä–∞–µ–º –æ—Ç—Å—Ç—É–ø—ã —É —Å–ø–∏—Å–∫–∞ */
}

.education-text li {
  margin-left: 0; /* –£–±–∏—Ä–∞–µ–º –æ—Ç—Å—Ç—É–ø —Å–ª–µ–≤–∞ –¥–ª—è —ç–ª–µ–º–µ–Ω—Ç–æ–≤ —Å–ø–∏—Å–∫–∞ */
  padding-left: 0; /* –£–±–∏—Ä–∞–µ–º –æ—Ç—Å—Ç—É–ø –¥–ª—è —Ç–µ–∫—Å—Ç–∞ —Ä—è–¥–æ–º —Å –º–∞—Ä–∫–µ—Ä–æ–º */
  font-size: 24px;
  line-height: 1.5; /* –£–≤–µ–ª–∏—á–∏–≤–∞–µ–º –º–µ–∂—Å—Ç—Ä–æ—á–Ω—ã–π –∏–Ω—Ç–µ—Ä–≤–∞–ª –¥–ª—è –ª—É—á—à–µ–≥–æ –≤—ã—Ä–∞–≤–Ω–∏–≤–∞–Ω–∏—è */
}

.education-title-1 {
  flex: 1;
  color: var(--blue);
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 10px;
  padding-right: 20px;
}

.education-title-1 h2 {
  font-family: 'TRIAL_Oceanic', sans-serif;
  font-weight: 800;
  font-size: 70px; 
}

/* –ú–æ–±–∏–ª—å–Ω–∞—è –∞–¥–∞–ø—Ç–∞—Ü–∏—è */
@media (max-width: 1360px) {
  .education-content {
    flex-direction: column; /* –í–µ—Ä—Ç–∏–∫–∞–ª—å–Ω–æ–µ —Ä–∞—Å–ø–æ–ª–æ–∂–µ–Ω–∏–µ –¥–ª—è –º–æ–±–∏–ª—å–Ω—ã—Ö */
    align-items: center; /* –¶–µ–Ω—Ç—Ä–∏—Ä–æ–≤–∞–Ω–∏–µ —ç–ª–µ–º–µ–Ω—Ç–æ–≤ */
    width: 90%; /* –§–∏–∫—Å–∏—Ä–æ–≤–∞–Ω–Ω–∞—è —à–∏—Ä–∏–Ω–∞ –¥–ª—è –∫–æ–Ω—Ç–µ–Ω—Ç–∞ */
    overflow-x: hidden; /* –°–∫—Ä—ã–≤–∞–µ—Ç –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω—ã–π —Å–∫—Ä–æ–ª–ª */
    padding: 10px 0px;
  }
  
  .education-text {
    margin-bottom: 12px;
  }
  .education-text p {
    font-size: 28px;
    margin-top: 20px;
    margin-bottom: 10px;
  }
  
  .education-text li {
    font-size: 24px;
  }
  .education-title-1 h2 {
    font-size: 70px; 
  }
  .education-title-1 {
    padding-top: 0px;
    padding-right: 0px;
  }
}

@media (max-width: 1050px) {
  .education-text p {
    font-size: 26px;
