.guides {
  padding: 50px;
  background-color: #ff8800;
  overflow-x: hidden; /* –°–∫—Ä—ã–≤–∞–µ—Ç –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω—ã–π —Å–∫—Ä–æ–ª–ª */
  padding: 10px 0px 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  
}

.guides-title {
  text-align: center;
  font-family: 'TRIAL_Oceanic', sans-serif;
  font-weight: 400;
  font-size: 70px; 
  color: rgb(210, 99, 89);
  width: 60%; /* –®–∏—Ä–∏–Ω–∞ —Ç–µ–∫—Å—Ç–∞ 60% */
  margin: 0 auto; /* –ì–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω–æ–µ –≤—ã—Ä–∞–≤–Ω–∏–≤–∞–Ω–∏–µ –ø–æ —Ü–µ–Ω—Ç—Ä—É */
}

.guides-title2 {
  text-align: center;
  font-size: 24px; /* –£–≤–µ–ª–∏—á–∏–≤–∞–µ–º —Ä–∞–∑–º–µ—Ä –∑–∞–≥–æ–ª–æ–≤–∫–∞ */
  font-family: "Raleway", sans-serif;
  font-weight: 400;
  font-style: normal;
  width: 60%; /* –®–∏—Ä–∏–Ω–∞ —Ç–µ–∫—Å—Ç–∞ 60% */
  margin: 10px auto 20px; /* –ì–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω–æ–µ –≤—ã—Ä–∞–≤–Ω–∏–≤–∞–Ω–∏–µ –ø–æ —Ü–µ–Ω—Ç—Ä—É */
}

.guides-list {
  display: flex;
  flex-direction: column; /* –†–∞—Å–ø–æ–ª–∞–≥–∞–µ–º –∫–∞—Ä—Ç–æ—á–∫–∏ –ø–æ –≤–µ—Ä—Ç–∏–∫–∞–ª–∏ */
  align-items: center; /* –¶–µ–Ω—Ç—Ä–∏—Ä—É–µ–º –∫–∞—Ä—Ç–æ—á–∫–∏ –ø–æ –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª–∏ */
  gap: 30px; /* –†–∞—Å—Å—Ç–æ—è–Ω–∏–µ –º–µ–∂–¥—É –∫–∞—Ä—Ç–æ—á–∫–∞–º–∏ */
}

.guide-item {
  position: relative;
  width: 550px; /* –£–≤–µ–ª–∏—á–∏–≤–∞–µ–º —à–∏—Ä–∏–Ω—É –∫–∞—Ä—Ç–æ—á–∫–∏ */
  height: 800px; /* –£–≤–µ–ª–∏—á–∏–≤–∞–µ–º –≤—ã—Å–æ—Ç—É –∫–∞—Ä—Ç–æ—á–∫–∏ */
  padding: 20px;
  background-color: var(--grey);
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
  border-radius: 30px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.guide-content h33 {
  font-size: 30px;
  font-family: "Raleway", sans-serif;
  font-weight: 400;
  font-style: normal;
  color: var(--blue);
}

.progress-bar {
  position: absolute;
  left: 0;
  top: 0;
  width: 6px; /* –£–≤–µ–ª–∏—á–∏–≤–∞–µ–º —à–∏—Ä–∏–Ω—É –ø—Ä–æ–≥—Ä–µ—Å—Å-–±–∞—Ä–∞ */
  background-color: var(--blue);
  z-index: 1; /* –ü—Ä–æ–≥—Ä–µ—Å—Å-–±–∞—Ä –±—É–¥–µ—Ç –ø–æ–≤–µ—Ä—Ö –∫–∞—Ä—Ç–æ—á–∫–∏ */
}

.guide-image {
  width: 100%;
  height: 80%; /* –£–≤–µ–ª–∏—á–∏–≤–∞–µ–º —Ä–∞–∑–º–µ—Ä –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è */
  object-fit: cover;
  border-radius: 40px;
  z-index: 0;
}

.guide-content {
  margin-top: 20px;
  text-align: center;
  z-index: 0;
}

.download-btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
  padding: 12px 10px;
  background-color: var(--grey); /* –ë–µ–ª—ã–π —Ñ–æ–Ω */
  color: var(--blue); /* –¶–≤–µ—Ç —Ç–µ–∫—Å—Ç–∞ —á–µ—Ä–Ω—ã–º */
  text-decoration: none;
  font-family: "Raleway", sans-serif;
  font-weight: 400;
  font-style: normal;
  border: 1px solid var(--blue); /* –ß–µ—Ä–Ω—ã–π –∫–æ–Ω—Ç—É—Ä –∫–Ω–æ–ø–∫–∏ */
  border-radius: 40px;
  font-size: 16px;
  
  text-transform: uppercase;
  letter-spacing: 1px;
  transition: background-color 0.4s ease, color 0.4s ease, border-color 0.4s ease; /* –ü–ª–∞–≤–Ω—ã–π –ø–µ—Ä–µ—Ö–æ–¥ */
}

.pdf-icon {
  filter: grayscale(); /* –ò–∫–æ–Ω–∫–∞ –≤ —Å–µ—Ä—ã—Ö —Ç–æ–Ω–∞—Ö */
  width: 20px;
  height: 20px;
  margin-left: 10px; /* –û—Ç—Å—Ç—É–ø –º–µ–∂–¥—É –∏–∫–æ–Ω–∫–æ–π –∏ —Ç–µ–∫—Å—Ç–æ–º */
  transition: filter 0.4s ease; /* –ü–ª–∞–≤–Ω—ã–π –ø–µ—Ä–µ—Ö–æ–¥ —Ñ–∏–ª—å—Ç—Ä–∞ */
}

.download-btn:hover {
  background-color: var(--blue); /* –ß–µ—Ä–Ω—ã–π —Ñ–æ–Ω */
  color: var(--white); /* –ë–µ–ª—ã–π —Ü–≤–µ—Ç —Ç–µ–∫—Å—Ç–∞ */
  border-color: #000000; /* –ë–µ–ª–∞—è –≥—Ä–∞–Ω–∏—Ü–∞ */
}

.download-btn:hover .pdf-icon {
  filter: grayscale(0%); /* –ò–∫–æ–Ω–∫–∞ —Å—Ç–∞–Ω–æ–≤–∏—Ç—Å—è —Ü–≤–µ—Ç–Ω–æ–π */
}

/* –ê–¥–∞–ø—Ç–∞—Ü–∏—è –¥–ª—è –º–æ–±–∏–ª—å–Ω—ã—Ö —É—Å—Ç—Ä–æ–π—Å—Ç–≤ */
