.slider {
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0px 0;
  background-color: deepskyblue;
}

.mySwiper {
  width: 60%; /* –û–≥—Ä–∞–Ω–∏—á–µ–Ω–∏–µ —à–∏—Ä–∏–Ω—ã –¥–ª—è –±–æ–ª—å—à–∏—Ö —ç–∫—Ä–∞–Ω–æ–≤ */
  height: auto; /* –£—Å—Ç–∞–Ω–æ–≤–∫–∞ –≤—ã—Å–æ—Ç—ã –Ω–∞ –∞–≤—Ç–æ –¥–ª—è –ª—É—á—à–µ–≥–æ –∞–¥–∞–ø—Ç–∏—Ä–æ–≤–∞–Ω–∏—è */
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-button-next,
.swiper-button-prev {
  color: var(--coral);
  transition: color 0.3s ease;
  width: 40px; /* –†–∞–∑–º–µ—Ä –∫–Ω–æ–ø–æ–∫ */
  height: 40px;
}

.swiper-button-next:hover,
.swiper-button-prev:hover {
  color: var(--blue); /* –¶–≤–µ—Ç –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏ */
}

.swiper-button-next::after,
.swiper-button-prev::after {
  font-size: 24px; /* –†–∞–∑–º–µ—Ä –∏–∫–æ–Ω–∫–∏ —Å—Ç—Ä–µ–ª–∫–∏ */
}

.swiper-pagination-bullet {
  background-color: var(--blue);
  opacity: 0.8;
}

.swiper-pagination-bullet-active {
  background-color: var(--coral);
  opacity: 1;
}

.slide-content {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 12px;
  overflow: hidden;
  transition: box-shadow 0.3s ease-in-out;
}

.slide-image {
  width: auto;
  height: 100%;
  object-fit:fill;
  border-radius: 0px;
}

/* –ú–æ–±–∏–ª—å–Ω–∞—è –∞–¥–∞–ø—Ç–∞—Ü–∏—è */
