/* –°—Ç–∏–ª–∏ –¥–ª—è —Ä–∞–∑–¥–µ–ª–∞ "–û–±—Ä–∞–∑–æ–≤–∞–Ω–∏–µ" */
.notworking {
  background-color: var(--blue);
  font-family: "Raleway", sans-serif;
  font-weight: 400;
  font-style: normal;
  background-color: blue;
  padding: 0 ; /* –û—Ç—Å—Ç—É–ø—ã —Å–≤–µ—Ä—Ö—É –∏ —Å–Ω–∏–∑—É */
}

.notworking-content {
  width: 60%; /* –§–∏–∫—Å–∏—Ä–æ–≤–∞–Ω–Ω–∞—è —à–∏—Ä–∏–Ω–∞ –¥–ª—è –∫–æ–Ω—Ç–µ–Ω—Ç–∞ */
  margin: 0px auto; /* –¶–µ–Ω—Ç—Ä–∏—Ä–æ–≤–∞–Ω–∏–µ –∫–æ–Ω—Ç–µ–Ω—Ç–∞ */
  padding: 20px; /* –û—Ç—Å—Ç—É–ø—ã –≤–Ω—É—Ç—Ä–∏ –±–ª–æ–∫–∞ */
  display: flex; /* –ò—Å–ø–æ–ª—å–∑—É–µ–º Flexbox –¥–ª—è —Ä–∞–∑–¥–µ–ª–µ–Ω–∏—è –Ω–∞ –¥–≤–µ —á–∞—Å—Ç–∏ */
  background-color: rgb(255, 133, 251);
}

.notworking-title-1 {
  font-size: 80px;
  flex: 1;
  order: 2;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 10px;
}



.notworking-text {
  flex: 1; /* –ó–∞–Ω–∏–º–∞–µ—Ç –ø–æ–ª–æ–≤–∏–Ω—É —à–∏—Ä–∏–Ω—ã */
  order: 1;
  color: black; /* –ß–µ—Ä–Ω—ã–π —Ç–µ–∫—Å—Ç */
  text-align: left; /* –í—ã—Ä–∞–≤–Ω–∏–≤–∞–Ω–∏–µ —Ç–µ–∫—Å—Ç–∞ –ø–æ –ª–µ–≤–æ–º—É –∫—Ä–∞—é */
}

.notworking-text h2 {
  margin-bottom: 10px; /* –û—Ç—Å—Ç—É–ø —Å–Ω–∏–∑—É –¥–ª—è –∑–∞–≥–æ–ª–æ–≤–∫–∞ */
}

.notworking-text p {
  margin-top: 0px;
  font-family: "Raleway", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 24px;
  color: var(--white);
}

.notworking-title-1 {
  flex: 1; /* –ó–∞–Ω–∏–º–∞–µ—Ç –≤—Ç–æ—Ä—É—é –ø–æ–ª–æ–≤–∏–Ω—É —à–∏—Ä–∏–Ω—ã */
  color: var(--coral);
  right: 5%; /* –†–∞—Å–ø–æ–ª–æ–∂–µ–Ω–∏–µ —Ç–µ–∫—Å—Ç–∞ –Ω–µ–º–Ω–æ–≥–æ –æ—Ç—Å—Ç—É–ø–∞–µ—Ç –æ—Ç –ø—Ä–∞–≤–æ–≥–æ –∫—Ä–∞—è */
  transform: translateY(40%); /* –¶–µ–Ω—Ç—Ä–∏—Ä–æ–≤–∞–Ω–∏–µ —Ç–µ–∫—Å—Ç–∞ –ø–æ –≤–µ—Ä—Ç–∏–∫–∞–ª–∏ */
}

.notworking-title-1 h2 {
  font-family: 'TRIAL_Oceanic', sans-serif;
  font-weight: 600;
  font-size: 70px; 
}

/* –ê–¥–∞–ø—Ç–∞—Ü–∏—è –ø–æ–¥ –º–æ–±–∏–ª—å–Ω—ã–µ —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞ */
@media (max-width: 1360px) {
  .notworking-content {
    flex-direction: column; /* –í–µ—Ä—Ç–∏–∫–∞–ª—å–Ω–æ–µ —Ä–∞—Å–ø–æ–ª–æ–∂–µ–Ω–∏–µ –¥–ª—è –º–æ–±–∏–ª—å–Ω—ã—Ö */
    align-items: center; /* –¶–µ–Ω—Ç—Ä–∏—Ä–æ–≤–∞–Ω–∏–µ —ç–ª–µ–º–µ–Ω—Ç–æ–≤ */
    width: 90%; /* –§–∏–∫—Å–∏—Ä–æ–≤–∞–Ω–Ω–∞—è —à–∏—Ä–∏–Ω–∞ –¥–ª—è –∫–æ–Ω—Ç–µ–Ω—Ç–∞ */
    overflow-x: hidden; /* –°–∫—Ä—ã–≤–∞–µ—Ç –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω—ã–π —Å–∫—Ä–æ–ª–ª */
    padding: 10px 0px;
  }
  .notworking-text p {
    margin-top: 0px;
    font-size: 24px;
    margin-bottom: 10px;
  }
  .notworking-title-1 h2 {
    font-size: 70px; 
  }
  .notworking-title-1 {
    order: 1;
  }
  .notworking-text {
    order: 2;
  }
}
@media (max-width: 1050px) {
  .notworking-text p {
    margin-top: 0px;
    font-size: 22px;
  }
  .notworking-title-1 h2 {
    font-size: 60px; 
  }
}
@media (max-width: 768px) {
  .notworking-text p {
    margin-top: 0px;
    font-size: 20px;
  }
  .notworking-title-1 h2 {
    font-size: 50px; 
  }
}
@media (max-width: 525px) {
  .notworking-text p {
    margin-top: 0px;
    font-size: 18px;
  }
  .notworking-title-1 h2 {
    font-size: 40px; 
  }

}
@media (max-width: 382px) {
  .notworking-text p {
    margin-top: 0px;
    font-size: 18px;
  }
  .notworking-title-1 h2 {
    font-size: 40px; 
  }
}
