.contact {
  padding: 0px 0; /* –û—Ç—Å—Ç—É–ø—ã —Å–≤–µ—Ä—Ö—É –∏ —Å–Ω–∏–∑—É */
  background-color: rgb(255, 204, 0);
}

.contact-container {
  width: 60%; /* –§–∏–∫—Å–∏—Ä–æ–≤–∞–Ω–Ω–∞—è —à–∏—Ä–∏–Ω–∞ –¥–ª—è –∫–æ–Ω—Ç–µ–Ω—Ç–∞ */
  margin: 0 auto; /* –¶–µ–Ω—Ç—Ä–∏—Ä–æ–≤–∞–Ω–∏–µ –∫–æ–Ω—Ç–µ–Ω—Ç–∞ */
  display: flex; /* –ò—Å–ø–æ–ª—å–∑—É–µ–º Flexbox –¥–ª—è —Ä–∞–∑–¥–µ–ª–µ–Ω–∏—è –Ω–∞ –¥–≤–µ —á–∞—Å—Ç–∏ */
  flex-wrap: wrap; /* –û–±–µ—Å–ø–µ—á–∏–≤–∞–µ–º –æ–±—Ç–µ–∫–∞–Ω–∏–µ —ç–ª–µ–º–µ–Ω—Ç–æ–≤ */
  background-color:cornflowerblue;
}



.contact-right {
  width: 50%;
  padding: 10px;
  align-items: center;
  justify-content: center;
}
.contact-left{
  flex: 1;
  align-items: center;
  justify-content: center;
}

#yandex-map {
  width: 100%;
  height: 400px; /* Set desired map height */
  border-radius: 25px; /* Add rounded corners */
  overflow: hidden; /* Prevent content overflow */
}

.contact-left h6 {
  margin:10px 0 40px 0;
  text-align: center;
  font-family: 'TRIAL_Oceanic', sans-serif;
  font-weight: 600;
  font-size: 70px; 
  color: var(--coral);
 
}

.contact-item {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}

.contact-item img {
  width: 25px;
  height: 25px;
  margin: 0px 15px 0px 25px;
}

.contact-link {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: inherit;
  transition: color 0.05s;
  font-family: 'TRIAL_Oceanic', sans-serif;
  font-weight: 500;
}

.contact-link:hover {
  color: #999999; /* Hover color */
}

.contact-item span {
  font-size: 30px;
  transition: transform 0.05s, color 0.05s ease-out;
}

/* Media Queries for Mobile Responsiveness */
@media (max-width: 1360px) {
  .contact-container {
    flex-direction: column; /* –í–µ—Ä—Ç–∏–∫–∞–ª—å–Ω–æ–µ —Ä–∞—Å–ø–æ–ª–æ–∂–µ–Ω–∏–µ –¥–ª—è –º–æ–±–∏–ª—å–Ω—ã—Ö */
    align-items: center; /* –¶–µ–Ω—Ç—Ä–∏—Ä–æ–≤–∞–Ω–∏–µ —ç–ª–µ–º–µ–Ω—Ç–æ–≤ */
    width: 90%; /* –§–∏–∫—Å–∏—Ä–æ–≤–∞–Ω–Ω–∞—è —à–∏—Ä–∏–Ω–∞ –¥–ª—è –∫–æ–Ω—Ç–µ–Ω—Ç–∞ */
    overflow-x: hidden; /* –°–∫—Ä—ã–≤–∞–µ—Ç –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω—ã–π —Å–∫—Ä–æ–ª–ª */
    padding: 10px 0px;
  }
  .contact-right {
    width: 90%;
    padding: 10px;
    margin:0px auto;
  }
  .contact-left{
    width: 90%;
    display: flex;
    flex-direction: column;
   
  }
  .contact-left h6 {
    margin:10px 0 20px 0;
    font-size: 70px; 
  }
  .contact-item img {
    width: 25px;
    height: 25px;
    margin: 0px 10px 0px 1px;
  }
  .contact-link {
    font-weight: 400;
  }
  .contact-item span {
    font-size: 26px;
  }
}
@media (max-width: 1050px) {
  .contact-left h6 {
    margin:10px 0 20px 0;
    font-size: 60px; 
  }
  .contact-item img {
    width: 22px;
    height: 22px;
    margin: 0px 10px 0px 1px;
  }
  .contact-link {
    font-weight: 300;
  }
  .contact-item span {
    font-size: 24px;
  }
}
@media (max-width: 768px) {
  .contact-left h6 {
    margin:10px 0 20px 0;
    font-size: 50px; 
  }
  .contact-item img {
    width: 20px;
    height: 20px;
    margin: 0px 10px 0px 1px;
  }
  .contact-link {
    font-weight: 300;
  }
  .contact-item span {
    font-size: 22px;
  }
}
@media (max-width: 525px) {
  .contact-left h6 {
    margin:10px 0 20px 0;
    font-size: 40px; 
  }
  .contact-item img {
    width: 18px;
    height: 18px;
    margin: 0px 10px 0px 1px;
  }
  .contact-link {
    font-weight: 300;
  }
  .contact-item span {
    font-size: 20px;
  }
}
@media (max-width: 382px) {
  .contact-left h6 {
    margin:10px 0 20px 0;
    font-size: 40px; 
  }
  .contact-item img {
    width: 18px;
    height: 18px;
    margin: 0px 10px 0px 1px;
  }
  .contact-link {
    font-weight: 200;
  }
  .contact-item span {
    font-size: 20px;
  }
}
